'use client';

import { useState, useCallback } from 'react';
import { AnalysisResponse, AnalysisRequest } from '@/types';
import { analyzeText } from '@/lib/api';
import Header from './Header';
import TextInput from './TextInput';

export function BiasDetectionApp() {
  const [text, setText] = useState('');
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [analysisResult, setAnalysisResult] = useState<AnalysisResponse | null>(null);
  const [error, setError] = useState<string | null>(null);

  const handleAnalyze = useCallback(async () => {
    if (!text.trim()) {
      setError('Please enter some text to analyze');
      return;
    }

    setIsAnalyzing(true);
    setError(null);

    try {
      const request: AnalysisRequest = {
        text: text.trim(),
        options: {
          sensitivity: 'medium',
          categories: ['gender_bias', 'racial_bias', 'religious_bias', 'general_bias']
        }
      };

      const result = await analyzeText(request);
      setAnalysisResult(result);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred during analysis');
      setAnalysisResult(null);
    } finally {
      setIsAnalyzing(false);
    }
  }, [text]);

  const handleClearText = useCallback(() => {
    setText('');
    setAnalysisResult(null);
    setError(null);
  }, []);

  return (
    <div className="min-h-screen bg-gray-50">
      <Header />

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 py-8">
        {/* Description */}
        <div className="text-center mb-8">
          <p className="text-lg text-gray-600">
            Analyze text for potential bias and get suggestions for improvement
          </p>
        </div>
          
          <div className="space-y-8">
          {/* Input Section */}
          <TextInput
            value={text}
            onChange={setText}
            onClear={handleClearText}
            onAnalyze={handleAnalyze}
            isAnalyzing={isAnalyzing}
            error={error}
            placeholder="Type or paste your text here..."
            label="Enter text to analyze"
            maxLength={5000}
            showCharacterCount={true}
            showClearButton={true}
            showAnalyzeButton={true}
            analyzeButtonText="Analyze Text"
            clearButtonText="Clear"
          />

          {/* Results Section */}
          {analysisResult && (
            <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
              <h2 className="text-xl font-semibold text-gray-900 mb-4">
                Analysis Results
              </h2>
              
              {/* Overall Score */}
              <div className="mb-6 p-4 bg-gray-50 rounded-lg">
                <div className="flex items-center justify-between">
                  <span className="text-sm font-medium text-gray-700">
                    Analysis Confidence:
                  </span>
                  <span className={`text-lg font-bold ${
                    (analysisResult.confidence || 0) > 0.7 
                      ? 'text-green-600' 
                      : (analysisResult.confidence || 0) > 0.5 
                        ? 'text-yellow-600' 
                        : 'text-red-600'
                  }`}>
                    {Math.round((analysisResult.confidence || 0) * 100)}%
                  </span>
                </div>
                <div className="mt-2 text-sm text-gray-600">
                  {analysisResult.flaggedWords.length === 0 && 'No issues detected'}
                  {analysisResult.flaggedWords.length > 0 && `${analysisResult.flaggedWords.length} potential issue${analysisResult.flaggedWords.length > 1 ? 's' : ''} found`}
                </div>
              </div>

              {/* Detected Issues */}
              {analysisResult.flaggedWords.length > 0 ? (
                <div>
                  <h3 className="text-lg font-medium text-gray-900 mb-3">
                    Detected Issues ({analysisResult.flaggedWords.length})
                  </h3>
                  <div className="space-y-3">
                    {analysisResult.flaggedWords.map((flaggedWord, index) => (
                      <div 
                        key={index}
                        className="p-4 border border-gray-200 rounded-lg"
                      >
                        <div className="flex items-start justify-between mb-2">
                          <div>
                            <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-red-100 text-red-800">
                              {flaggedWord.category}
                            </span>
                            <span className="ml-2 text-sm text-gray-600">
                              Confidence: {Math.round(flaggedWord.confidence * 100)}%
                            </span>
                          </div>
                        </div>
                        
                        <div className="mb-2">
                          <span className="text-sm font-medium text-gray-700">Flagged text: </span>
                          <span className="text-sm bg-yellow-100 px-1 rounded">
                            &quot;{flaggedWord.word}&quot;
                          </span>
                        </div>
                        
                        {flaggedWord.explanation && (
                          <div className="mb-2 text-sm text-gray-600">
                            <span className="font-medium">Explanation: </span>
                            {flaggedWord.explanation}
                          </div>
                        )}
                        
                        {flaggedWord.suggestions && flaggedWord.suggestions.length > 0 && (
                          <div>
                            <span className="text-sm font-medium text-gray-700">Suggestions: </span>
                            <div className="mt-1 flex flex-wrap gap-1">
                              {flaggedWord.suggestions.map((suggestion, idx) => (
                                <button
                                  key={idx}
                                  className="inline-flex items-center px-2 py-1 text-xs font-medium bg-blue-100 text-blue-800 rounded hover:bg-blue-200 focus:outline-none focus:ring-2 focus:ring-blue-500"
                                  onClick={() => {
                                    const newText = text.replace(flaggedWord.word, suggestion.word);
                                    setText(newText);
                                  }}
                                                                  >
                                    &quot;{suggestion.word}&quot;
                                  </button>
                              ))}
                            </div>
                          </div>
                        )}
                      </div>
                    ))}
                  </div>
                </div>
              ) : (
                <div className="text-center py-8">
                  <div className="text-green-600 text-lg font-medium mb-2">
                    ✓ No bias detected
                  </div>
                  <div className="text-gray-600">
                    The text appears to be free from detectable bias patterns.
                  </div>
                </div>
              )}
            </div>
          )}
          </div>
      </main>
    </div>
  );
} 